.single-user-inner-content{
    background-color: #efefef;
    border-radius: 5px;
    cursor: pointer;
    padding: 15px 2px;
    transition: 0.5s;
}

.single-user-inner-content:hover{
    box-shadow: 0 2px 20px #333;
}

.user-avatar{
    width: 50%;
    height: auto;
    border-radius: 50%;
}